.section{
    display: flex;
    align-items: center;
    border-bottom-width:2px ;
    min-width: 220px;
    cursor: pointer;
}

.section--selected{
    background-color: whitesmoke;
    border-width: 3px !important;
}

.section:hover{
    background-color: whitesmoke;
    border-width: 3px !important;
}

.section > h4 {
    font-size: 12px;
    margin-left: 15px;
}